* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
input,
button {
  font-size: 12px;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
}

h1 {
  font-size: 40px;
}

.App {
  font-family: sans-serif;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.App header {
  width: 50%;
  margin: 84px 0 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.App header img {
  width: 60px;
  heigth: 60px;
  border-radius: 30px;
  margin-left: 7px;
}

.App header div {
  display: flex;
  align-items: center;
}

.App input {
  width: 50%;
  padding: 24px;

  background: #e6e6e6;
  border-radius: 5px;
  border: none;

  font-size: 16px;
}
.App input:hover {
  filter: brightness(0.95);
}

.App button {
  width: 50%;
  padding: 24px;
  font-weight: 700;

  background: #8b008b;
  color: #ffffff;

  border-radius: 5px;
  margin: 12px 0 84px;
  border: none;
  font-size: 16px;
  cursor: pointer;
  transition: filter 0.2s;
}

.App button:hover {
  filter: brightness(0.7);
}
